/**
 * Bio component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"


import { useStaticQuery, graphql } from "gatsby"
import Image from "gatsby-image"

const Stack = () => {
  const data = useStaticQuery(graphql`
  query Stack {
    avatar: file(absolutePath: { regex: "/profile-pic.png/" }) {
      childImageSharp {
        fluid(maxWidth: 400, grayscale: true) {
          ...GatsbyImageSharpFluid_noBase64
        }
      }
    }
  }
`)
  return (
    <header class="text-black border-b border-black">
        <div class="flex flex-col md:flex-row">
        <div class="md:w-6/12 border-r border-black">
                <div class="bg-gray-50 mt-12 mb-12 md:py-2 sm:py-2 p-8 sm:grid sm:grid-cols-1 mb-4 sm:gap-1 ">
                <div class="text-5xl">
                  Stack</div>
                  <div class="text-sm text-gray-700 mt-6">
                  Development
                  </div>

              <div class="text-xl">
               CSS, Gatsby, HTML, Javascript, Jekyll, React, Ruby on Rails, NodeJS, Wordpress
               </div>
               <div class="text-sm text-gray-700 mt-6">
                Data Analysis</div>
                <div class="text-xl">
                 R, Python, STATA
                </div>
                </div>
             </div>

            <div class="md:w-6/12 ">
                <div class="sm:grid sm:grid-cols-1 sm:gap-4">
                  <Image
                      fluid={data.avatar.childImageSharp.fluid}/>
           </div>
        </div>
    </div>
</header>
  )
}

export default Stack
